import React, { useEffect, useState } from 'react';
import ErrorModal from '../../Shared/Components/UIElements/ErrorModal';
import {useHttpClient} from '../../Shared/hooks/http-hook';
import UserList from '../Components/UserList';
import Spinner from './../../Shared/Components/UIElements/Spinner';

function Users(props) {

  const [users, setUsers] = useState([]);
  const {loading, errorState, sendRequest, cancelError} = useHttpClient()
  
  useEffect(() => {
    const fetchUsers = async()=> {
      try {
        const data = await sendRequest(process.env.REACT_APP_BACKEND_URL+'/users')
        setUsers(data.users)
      } catch (error) {
      }
      
    }
    fetchUsers()
    


  }, [sendRequest])
  return (
    <>

     { loading && 
     <Spinner asOverlay />}
      <ErrorModal error={errorState} onClear={cancelError} />
     {!loading && <UserList items={users} />}
    </>
  );
}

export default Users;